.border {
  width: 40%;
  padding: 30px;
  border: 2px solid black;
  background-color: #0ca2ec;
}

.form {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  font-size: 25px;
  
}

.button {
  font-size: 15px;
  width: 150px;
  height: 35px;
  background-color: #0f09ac;
  color: aqua;
}
.button:hover{
  background-color: #d1fa54;
  color: rgb(4, 0, 255);
}

.label {
  font-size: 20px;
  margin-right: 20px;
}

.input {
  font-size: 20px;
  margin-bottom: 30px;
  width: 200px;
}